import { strTrim } from './str-trim.js';

/**
 * @description
 * Convert string to camel case format
 *
 * @example
 * ``` ts
 * strKebabCase('Foo Bar', true);
 * // => foo-bar
 * strKebabCase('Foo bar', true);
 * // => foo-bar
 * strKebabCase('Foo bar', false);
 * // => Foo-bar
 * ```
 *
 * @param str The string to be converted
 * @param lowerCase If need to convert to lower case
 *
 * @docsCategory preference/utils/string
 * @codeFilter str-kebab-case
 */
export const strKebabCase = (str: string, lowerCase = false) => {
  str = str.replace(/[!"'(),â€“.:;<>?`{}|~/\\[\]_#$*&^@%]+/g, ' ');
  str = str.replace(/([a-z\d])([A-Z])/g, '$1 $2');
  str = strTrim(str).replace(/\s+/g, '-');
  return lowerCase ? str.toLowerCase() : str;
};
